$container-gutter: 50px;
$container-width: calc(1200px + #{$container-gutter} * 2);
$col-gutter: $container-gutter;

.clearfix {
  @include clearfix;
}

.container {
  @include clearfix;

  display: block;
  padding-left: $container-gutter;
  padding-right: $container-gutter;
  margin-left: auto;
  margin-right: auto;

  @media #{$m-to-m} {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.row {
  @include clearfix;

  display: block;
  margin-left: -$col-gutter;
  margin-right: -$col-gutter;
}

.col {
  display: block;
  float: left;
  padding-right: $col-gutter;
  padding-left: $col-gutter;
}

.page {
  display: flex;
  flex-direction: column;
}

.t-center {
  text-align: center;
}



