.partner {
  &__items {
      width: 60%;

      @media #{$m-to-l} {
        width: 100%;
      }

  }

  &__item {
    display: flex;
    margin: 60px 0;
  }

  &__logo {
    margin-right: 60px;
  }
}

.partners {
  position: relative;
  padding-top: 17.1875vw;

  @media #{$m-to-l} {
    padding-top: 0;
  }

  &__inner {
    display: flex;
  }

  &__header {
    padding-top: 100px;

    @media #{$m-to-l} {
      padding-top: 50px;
    }

  }

  &__elements {
    width: 40%;
    position: relative;

    @media #{$m-to-l} {
      display: none;
    }

  }
}


