/* ==========================================================================
   Base styles
   ========================================================================== */
/* stylelint-disable selector-max-universal  */
*,
*::before,
*::after {
  box-sizing: border-box;
}
/* stylelint-enable selector-max-universal */


html {
  font-family: $main-font;
  font-size: 62.5%;
  line-height: $main-line-height;
  color: $main-text-color;
  overflow-wrap: break-word;
  word-wrap: break-word;
  height: 100%;
}

body {
  background: $main-bg;
  font-size: 160%;
  height: 100%;
}

audio,
canvas,
iframe,
img,
svg,
video,
object,
input,
textarea {
  vertical-align: middle;
  max-width: 100%;
}

img {
  height: auto;
}

textarea {
  resize: vertical;
}

button,
input[type="submit"] {
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}

.rectangle {
  display: block;
  padding-top: 50px;
  position: relative;
}

.rectangle::before {
  content: '';
  position: absolute;
  top: 0;
  display: inline-block;
  width: 48px;
  height: 38px;
  border: 5px solid $c-primary;
}



