.menu {
  margin: 0;
  padding: 0;

  &-item {
    list-style-type: none;
    display: inline-block;
    text-transform: uppercase;
    margin: 0 15px;

    &.active .dot {
      background-color: $c-primary;
    }

    @media #{$m-to-l} {
      display: block;
      margin: 15px;
    }

    a:hover {
      color: $c-primary;
    }
  }
}

.hamburger {
  cursor: pointer;
  width: 35px;
  position: relative;
  transform: rotate(0deg);
  height: 26px;
  transition: 0.5s ease-in-out;
  z-index: 9999999;
  display: none;

  @media #{$m-to-l} {
    display: block;
  }

  div {
    height: 4px;
    width: 100%;
    opacity: 1;
    left: 0;
    position: absolute;
    border-radius: 5px;
    background-color: $c-primary;
    transform: rotate(0deg);
    transition: 0.25s ease-in-out;

    &:nth-child(1) {
      top: 0;
    }

    &:nth-child(2),
    &:nth-child(3) {
      top: 11px;
    }

    &:nth-child(4) {
      top: 22px;
    }
  }

  &.open {
    div {
      &:nth-child(1) {
        top: 18px;
        width: 0;
        left: 50%;
      }

      &:nth-child(2) {
        transform: rotate(45deg);
      }

      &:nth-child(3) {
        transform: rotate(-45deg);
      }

      &:nth-child(4) {
        top: 18px;
        width: 0;
        left: 50%;
      }
    }
  }
}

.dot {
  position: fixed;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: #E6E9EA;
}

.line {
  position: absolute;
  height: 100%;
  width: 1px;
  background-color: #E6E9EA;
  z-index: -1;
}

.content * {
  position: relative;
  z-index: 1;
}


