.footer__links {
  color: #707070;
  text-transform: uppercase;
  text-align: center;

  a {
    padding: 0 10px;
    position: relative;

    &::before {
      bottom: -15px;
      left: 50%;
      transform: translate(-50%, 0);
      content: '';
      position: absolute;
      height: 10px;
      width: 10px;
      border-radius: 50%;
      background-color: #E6E9EA;
    }

    &::after {
      content: '';
      position: absolute;
      height: 100%;
      width: 1px;
      bottom: -30px;
      left: 50%;
      transform: translate(-50%, 0);
      background-color: #E6E9EA;
      z-index: -1;
    }
  }
}


div.footer {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%, 0);
}


