.about {
  position: relative;
  padding-top: 200px;

  @media #{$m-to-l} {
    padding-top: 50px;
  }

  &__content {
    font-weight: 300;
    width: 55%;

    @media #{$m-to-m} {
      width: 100%;
      margin-bottom: 30px;
    }
  }

  &__description {
    margin-bottom: 15px
  }

  &__inner {
    display: flex;
    align-items: center;

    @media #{$m-to-m} {
      
    flex-wrap: wrap;
    }
  }

  &__img {
    position: relative;
    right: calc(50% - 50vw);
    width: 50vw;

    @media #{$m-to-m} {
      width: 100%;
    }

  }
}

.about-features {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &__item {
    display: flex;
    font-size: 22px;
    font-weight: 300;
  }

  &__icon {
    color: $c-primary;
    margin-right: 15px;
  }
}


