h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $heading-font;
  font-weight: 600;
}

h1 { 
  font-size: 4rem;
  margin-bottom: 15px;
  line-height: 1.2;
}

h2 {
  font-size: 3.2rem;
  margin-bottom: 15px;
}

h4 {
  font-weight: 600;
  font-size: 1.8rem;
  margin-bottom: 10px;
}



