.cover {
  &__inner {
    display: flex;
    align-items: center;

    @media #{$m-to-2l} {
      flex-wrap: wrap;
    }
  }

  &__content {
    width: 480px;
    max-width: 100%;
    font-weight: 300;

    @media #{$m-to-2l} {
      width: 100%!important;
    }
  }

  &__description {
    margin-bottom: 30px;
  }
}

.cover-slider {

  &__wrap {
    position: relative;
    left: calc(50% - 50vw);
    width: calc(50% - 480px + 50vw);
    padding-right: 100px;
    max-width: 100%;

    @media #{$m-to-2l} {
      width: calc(100% + 100px)!important;
      left: -50px;
      padding-right: 0;
      margin-top: 80px;
    }
  }

  &__item {
    img {
      @include object-fit(cover);

      width: 100%;
      max-height: calc(100vh - 100px);
    }
  }

  &__pagination {
    display: flex;
    justify-content: center;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 10px;

    &--devider {
      color: $c-primary;
    }

    &--total {
      color: #B1ABAB;
    }
  }

  &__text {
    position: absolute;
    top: 0;
    right: 60px;
    height: 70%;

    svg {
      height: 100%;
    }
  }

  .slick-arrow {
    display: inline-block;
    font: normal normal normal 20px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    border: 2px solid $c-primary;
    top: inherit;
    bottom: 80px;
  }

  .slick-arrow::before {
    font-family: inherit;
    font-weight: 700;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(calc(-50% + 2px), calc(-50% - 2px));
    font-size: 30px;
    color: $c-primary;
  }

  .slick-prev {
    right: 20px;
    left: inherit;
    z-index: 1;
    position: absolute;
    z-index: 3;
  }

  .slick-next {
    left: calc(100% + 20px);
  }

  .slick-next::before {
    content: "\F105";
  }

  .slick-prev::before {
    transform: translate(calc(-50% - 2px), calc(-50% - 2px));
    content: "\F104";
  }

  .slick-arrow.slick-disabled {
    border-color: #E6E9EA;
  }

  .slick-arrow.slick-disabled:before {
    color: #E6E9EA;
    opacity: 1;
  }
}


