.clients {
  padding: 50px 0;
  position: relative;

  &__shadow {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 50%;

    @media #{$m-to-2l} {
      display: none;
    }
  }

  img {
    filter: grayscale(100%);
    transition: 0.5s;

    &:hover {
      filter: grayscale(0%);
    }
  }

  .slick-current img {
    filter: grayscale(0%);
  }

  &__pagination {
    display: flex;
    justify-content: center;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 10px;
    margin-bottom: 50px;

    &--devider {
      color: $c-primary;
    }
  }

  .slick-arrow {
    display: inline-block;
    font: normal normal normal 20px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    border: 2px solid $c-primary;
    top: -80px;

    &::before {
      font-family: inherit;
      font-weight: 700;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(calc(-50% + 2px), calc(-50% - 2px));
      font-size: 30px;
      color: $c-primary;
    }
  }

  .slick-prev {
    left: 50%;
    transform: translate(calc(-50% - 60px), 0);
  }

  .slick-next {
    right: 50%;
    transform: translate(calc(-50% + 80px), 0);
    position: absolute;
    z-index: 3;
  }

  .slick-next::before {
    content: "\F105";
  }

  .slick-prev::before {
    transform: translate(calc(-50% - 2px), calc(-50% - 2px));
    content: "\F104";
  }

  .slick-arrow.slick-disabled {
    border-color: #E6E9EA;
  }

  .slick-arrow.slick-disabled:before {
    color: #E6E9EA;
    opacity: 1;
  }
}


