.contact-form {
  background-image: url('../img/element-6.png');
  height: 80vw;
  padding-top: 25vw;
  padding-bottom: 10px;
  background-repeat: no-repeat;
  background-size: 100%;

  &__title {
    margin-bottom: 100px;
  }

  input[type="text"],
  input[type="email"],
  input[type="phone"],
  textarea {
    border: 0;
    border-bottom: 1px solid $c-primary;
    width: 250px;
    color: #707070;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 300;
    margin: 0 40px 80px 0;

    &:focus {
      outline: none;
    }

    &::placeholder {
      color: #707070;
      text-transform: uppercase;
      letter-spacing: 2px;
      font-weight: 300;
    }
  }

  input[type='submit'] {
    border: 0;
  }

  .textarea {
    width: 550px;
    background: transparent;

    &__wrap {
      position: relative;
    }

    &__placeholder {
      position: absolute;
      bottom: 90px;
      color: #707070;
      text-transform: uppercase;
      font-weight: 700;
      z-index: -1;
      font-weight: 300;
    }
  }

  .checkbox {
    width: 580px;
    margin-bottom: 30px;

    max-width: 100%;
  }
}


