.button { 
  background-color: $c-primary;
  color: #fff;
  box-shadow: 1px 3px 12px rgba(33, 63, 160, 0.65);
  border-radius: 11px;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 25px 45px;
  display: inline-block;
  letter-spacing: 1px;
  transition: 0.5s;

  i {
    transition: 0.5s;
    position: relative;
    left: 0;
  }

  &:hover {
    background-color: $c-red;

    i {
      left: 10px;
    }
  }

  &--white {
    background-color: #fff;
    color: $c-red;
    border: 0;
    width: 100%;


    &:hover {
      background-color: #fff;
    }
  }
}


