.team {
  padding: 315px 0 50px;
  position: relative;

  @media #{$m-to-l} {
    padding-top: 50px;
  }

  &__content {
    width: 55%;
    font-weight: 300;
    margin-bottom: 40px;

    @media #{$m-to-m} {
      width: 100%;
    }

  }

  &__header {
    display: flex;
    justify-content: space-between;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
    z-index: 0;


    @media #{$m-to-l} {
      justify-content: space-around;
    
    }
  }

  &__item {
    width: 20%;
    padding: 0 20px;
    text-align: center;

    @media #{$m-to-l} {
      width: 33.33%;
      padding: 20px 20px;

    }

    @media #{$m-to-m} {
      width: 50%;

    }
  }

  &__contacts {
    color: $c-primary;
  }

  &__img {
    margin-bottom: 15px;

    img {
      width: 100%;
    }
  }

  &__name {
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 5px;
  }

  &__description {
    font-weight: 300;
    font-size: 14px;
    margin-bottom: 10px;
  }
}

.contacts {
  margin: 0 -10px;

  a {
    font-size: 20px;
    margin: 0 10px;
    transition: 0.3s;
    color: $c-primary;

    &:hover {
      color: $c-red;
    }
  }
}

.team-contact-form {
  position: relative;
  height: 100%;

  &:hover {
    .team-contact-form__wrap {
      opacity: 1;
      visibility: visible;
      cursor: pointer;
    }
  }

  &__inner {
    background-color: $c-red;
    box-shadow: 0 5px 12px rgba(5, 37, 67, 0.7);
    padding: 20px 10px;
  }

  &__icon  {
    position: relative;
    display: inline-block;

    img {
      animation: shake 4.72s ease infinite;
      transform-origin: 50% 50%;
    }

    span {
      right: -3px;
      position: absolute;
      width: 20px;
      height: 20px;
      display: inline-block;
      background-color: $c-red;
      border-radius: 50%;
      color: #fff;
      text-align: center;
      line-height: 20px;
      font-size: 17px;
      font-weight: 600;
    }
  }

  &__submit {
    text-align: center;
    margin-top: 30px;
  }

  &__submit-inner {
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: inline-block;
    position: relative;

    &:hover {
      &::before {
        right: 20px;
      }
    }

    &::before {
      content: "\F061";
      position: absolute;
      top: 50%;
      right: 30px;
      transform: translate(0, -50%);
      color: $c-red;
      transition: 0.5s;
    }
  }

  &__wrap {
    width: 350px;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;
    right: 0;
    padding-right: 50px;
    top: 0;
    position: absolute;
    color: #fff;
    font-weight: 300;
    font-size: 1.2rem;
  }

  input[type='text'] {
    width: 100%;
    border: 0;
    border-bottom: 1px solid #fff;
    background-color: $c-red;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 300;
    padding: 10px 0;
    margin-bottom: 20px;

    &::placeholder {
      color: #fff;
      text-transform: uppercase;
      letter-spacing: 2px;
      font-weight: 300;
    }
  }

  input[type='submit'] {
    width: auto;
    margin: 0 auto;
    padding: 25px 50px;
  }

  label {
    display: block;
    position: relative;
    padding-left: 28px;
    margin-bottom: 12px;
    cursor: pointer;
    user-select: none;

    input:checked ~ .checkmark:after {
      display: block;
    }
  }

  input[type='checkbox'] {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 18px;
    width: 18px;
    border-radius: 3px;
    border: 1px solid #fff;
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    &::after {
      position: absolute;
      display: none;
      content: "\f00c";
      top: 2px;
      left: 2px;
    }
  }
}

@keyframes shake {
 0% { transform:translate(0,0) }
  1.78571% { transform:translate(5px,0) }
  3.57143% { transform:translate(0,0) }
  5.35714% { transform:translate(5px,0) }
  7.14286% { transform:translate(0,0) }
  8.92857% { transform:translate(5px,0) }
  10.71429% { transform:translate(0,0) }
  100% { transform:translate(0,0) }
}


