.header {
  width: 100%;

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 0;
  }

  &__nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    @media #{$m-to-l} {
      position: fixed;
      top: 0;
      background-color: #fff;
      height: 100vh;
      color: $c-primary;
      width: 100vw;
      padding-top: 100px;
      right: -100vw;
      transition: $default-transition;
      flex-direction: column;
      justify-content: flex-start;
      text-align: center;
      z-index: 999999;

      &.active {
        right: 0;
      }
    }
  }

  &__phone {
    position: relative;
    transition: 0.3s;
    top: 0;

    &:hover {
      top: -5px
    }
  }

  &__logo {

    img {
      height: 40px;
    }
  }
}


